html, body {
  max-width: 100%;
  overflow-x: hidden; }

* {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

.hide {
  display: none; }

.show {
  display: block; }

.about_headerBlock {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box; }
  .about_headerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    padding: 20px 15px; }
    .about_headerBlock_wrapper_logo {
      display: flex;
      align-items: center; }
      .about_headerBlock_wrapper_logo_block {
        background-color: black;
        width: 80px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-family: RobotoRegular;
        font-size: 20px;
        margin-right: 23px; }
      .about_headerBlock_wrapper_logo_h1 {
        font-family: RobotoRegular;
        font-size: 22px;
        color: black;
        font-weight: normal; }
    .about_headerBlock_wrapper_links {
      display: flex;
      max-width: 300px;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-left: 120px; }
      .about_headerBlock_wrapper_links_link {
        font-family: RobotoRegular;
        font-size: 20px;
        color: black; }
    .about_headerBlock_wrapper_icon {
      display: none;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-right: 30px; }
  @media screen and (min-width: 768px) {
    .about_headerBlock_wrapper_icon {
      display: none; } }
  @media screen and (max-width: 768px) {
    .about_headerBlock_wrapper_links {
      display: none; }
    .about_headerBlock_wrapper_icon {
      display: block; }
    .about_headerBlock_wrapper {
      justify-content: space-between; } }

.about_navBlock {
  width: 100%;
  max-width: 1920px;
  height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box;
  background-color: white; }
  .about_navBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 15px; }
    .about_navBlock_wrapper_link {
      color: black;
      font-family: RobotoRegular;
      font-size: 30px;
      font-weight: normal;
      margin-top: 15px; }

.about_aboutBlock {
  max-width: 1920px;
  width: 100%;
  margin: 50px auto;
  box-sizing: border-box; }
  .about_aboutBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    color: black;
    font-size: 20px;
    font-family: RobotoRegular;
    box-sizing: border-box; }
    .about_aboutBlock_wrapper_p_link {
      cursor: pointer;
      text-decoration: underline; }
  @media screen and (max-width: 1280px) {
    .about_aboutBlock_wrapper {
      padding: 0 15px; } }

.about_mapBlock {
  max-width: 1920px;
  width: 100%;
  margin: 50px auto; }
  .about_mapBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box; }
    .about_mapBlock_wrapper_locationText {
      font-family: RobotoRegular;
      font-size: 25px;
      color: black; }
    .about_mapBlock_wrapper_map {
      position: relative;
      overflow: hidden;
      width: 100%;
      padding-top: 56.25%; }
      .about_mapBlock_wrapper_map_iframe {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%; }
  @media screen and (max-width: 1280px) {
    .about_mapBlock_wrapper {
      padding: 0 15px; } }

.about_footerBlock {
  max-width: 1920px;
  width: 100%;
  margin-top: 100px;
  margin-bottom: 50px;
  box-sizing: border-box; }
  .about_footerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    font-family: RobotoRegular;
    font-size: 10px;
    color: black; }
  @media screen and (max-width: 1280px) {
    .about_footerBlock {
      padding: 0 15px; } }
