html, body {
  max-width: 100%;
  overflow-x: hidden; }

* {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

.hide {
  display: none; }

.show {
  display: block; }

.index_headerBlock {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box; }
  .index_headerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    padding: 20px 15px; }
    .index_headerBlock_wrapper_logo {
      display: flex;
      align-items: center; }
      .index_headerBlock_wrapper_logo_block {
        background-color: black;
        width: 80px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-family: RobotoRegular;
        font-size: 20px;
        margin-right: 23px; }
      .index_headerBlock_wrapper_logo_h1 {
        font-family: RobotoRegular;
        font-size: 22px;
        color: black;
        font-weight: normal; }
    .index_headerBlock_wrapper_links {
      display: flex;
      max-width: 300px;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-left: 120px; }
      .index_headerBlock_wrapper_links_link {
        font-family: RobotoRegular;
        font-size: 20px;
        color: black; }
    .index_headerBlock_wrapper_icon {
      display: none;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-right: 30px; }
  @media screen and (min-width: 768px) {
    .index_headerBlock_wrapper_icon {
      display: none; } }
  @media screen and (max-width: 768px) {
    .index_headerBlock_wrapper_links {
      display: none; }
    .index_headerBlock_wrapper_icon {
      display: block; }
    .index_headerBlock_wrapper {
      justify-content: space-between; } }

.index_navBlock {
  width: 100%;
  max-width: 1920px;
  height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box;
  background-color: white; }
  .index_navBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 15px; }
    .index_navBlock_wrapper_link {
      color: black;
      font-family: RobotoRegular;
      font-size: 30px;
      font-weight: normal;
      margin-top: 15px; }

.index_offerBlock {
  background-color: #021628;
  max-width: 1920px;
  width: 100%;
  padding: 75px 0;
  overflow-x: hidden;
  box-sizing: border-box; }
  .index_offerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between; }
    .index_offerBlock_wrapper_container {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      min-height: 320px; }
      .index_offerBlock_wrapper_container_h2 {
        color: white;
        font-family: RobotoRegular;
        font-size: 40px; }
      .index_offerBlock_wrapper_container_description {
        color: white;
        font-family: RobotoRegular;
        font-size: 15px;
        width: 300px; }
      .index_offerBlock_wrapper_container_btn {
        color: black;
        font-family: RobotoRegular;
        font-size: 15px;
        background-color: white;
        width: 225px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center; }
  @media screen and (max-width: 1280px) {
    .index_offerBlock_wrapper {
      padding: 0 15px; }
      .index_offerBlock_wrapper_img {
        margin-right: 15px; } }
  @media screen and (max-width: 768px) {
    .index_offerBlock_wrapper_img {
      width: 300px;
      height: auto; } }
  @media screen and (max-width: 480px) {
    .index_offerBlock_wrapper {
      box-sizing: border-box; }
      .index_offerBlock_wrapper_container_h2 {
        width: 100%; }
      .index_offerBlock_wrapper_container_description {
        width: 100%; }
      .index_offerBlock_wrapper_container_btn {
        width: 100%; }
      .index_offerBlock_wrapper_img {
        display: none; } }

.index_itemsBlock {
  max-width: 1920px;
  width: 100%;
  box-sizing: border-box; }
  .index_itemsBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto; }
    .index_itemsBlock_wrapper_h2 {
      color: black;
      font-family: RobotoRegular;
      font-size: 40px;
      margin-top: 30px; }
    .index_itemsBlock_wrapper_items {
      margin-top: 50px;
      display: grid;
      box-sizing: border-box;
      grid-template-columns: 15% 15% 15% 15% 15% 15%;
      grid-column-gap: 1.5%; }
      .index_itemsBlock_wrapper_items_itemBlock {
        box-sizing: border-box;
        margin-top: 50px;
        background-color: #ECE7E7; }
        .index_itemsBlock_wrapper_items_itemBlock_top {
          background-color: #3B9C73;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 25px 0; }
          .index_itemsBlock_wrapper_items_itemBlock_top_img {
            width: 75%; }
        .index_itemsBlock_wrapper_items_itemBlock_down {
          padding: 15px;
          padding-bottom: 0px; }
          .index_itemsBlock_wrapper_items_itemBlock_down_name {
            color: black;
            font-family: RobotoRegular;
            font-size: 15px; }
          .index_itemsBlock_wrapper_items_itemBlock_down_btn {
            margin-top: 30px;
            font-size: 15px;
            color: black;
            font-family: RobotoRegular;
            padding: 5px;
            border-radius: 5%;
            background-color: #C4C4C4;
            display: flex;
            justify-content: center;
            align-items: center; }
        @media screen and (max-width: 1000px) {
          .index_itemsBlock_wrapper_items_itemBlock_down_btn {
            font-size: 7px; } }
        @media screen and (max-width: 768px) {
          .index_itemsBlock_wrapper_items_itemBlock_down_btn {
            font-size: 12px; } }
        @media screen and (max-widht: 320px) {
          .index_itemsBlock_wrapper_items_itemBlock_down_btn {
            font-size: 7px; } }
      @media screen and (max-width: 768px) {
        .index_itemsBlock_wrapper_items {
          grid-template-columns: 30% 30% 30%;
          grid-column-gap: 3%; } }
      @media screen and (max-width: 480px) {
        .index_itemsBlock_wrapper_items {
          margin-top: 20px;
          grid-template-columns: 45% 45%;
          grid-column-gap: 5%; } }
  @media screen and (max-width: 1280px) {
    .index_itemsBlock {
      padding: 0 15px; } }
  @media screen and (max-width: 480px) {
    .index_itemsBlock_wrapper_h2 {
      font-size: 25px; } }

.index_footerBlock {
  max-width: 1920px;
  width: 100%;
  margin-top: 100px;
  margin-bottom: 50px;
  box-sizing: border-box; }
  .index_footerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    font-family: RobotoRegular;
    font-size: 10px;
    color: black; }
  @media screen and (max-width: 1280px) {
    .index_footerBlock {
      padding: 0 15px; } }
