html, body {
  max-width: 100%;
  overflow-x: hidden; }

* {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

.hide {
  display: none; }

.show {
  display: block; }

.item_headerBlock {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box; }
  .item_headerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    padding: 20px 15px; }
    .item_headerBlock_wrapper_logo {
      display: flex;
      align-items: center; }
      .item_headerBlock_wrapper_logo_block {
        background-color: black;
        width: 80px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-family: RobotoRegular;
        font-size: 20px;
        margin-right: 23px; }
      .item_headerBlock_wrapper_logo_h1 {
        font-family: RobotoRegular;
        font-size: 22px;
        color: black;
        font-weight: normal; }
    .item_headerBlock_wrapper_links {
      display: flex;
      max-width: 300px;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-left: 120px; }
      .item_headerBlock_wrapper_links_link {
        font-family: RobotoRegular;
        font-size: 20px;
        color: black; }
    .item_headerBlock_wrapper_icon {
      display: none;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-right: 30px; }
  @media screen and (min-width: 768px) {
    .item_headerBlock_wrapper_icon {
      display: none; } }
  @media screen and (max-width: 768px) {
    .item_headerBlock_wrapper_links {
      display: none; }
    .item_headerBlock_wrapper_icon {
      display: block; }
    .item_headerBlock_wrapper {
      justify-content: space-between; } }

.item_navBlock {
  width: 100%;
  max-width: 1920px;
  height: 100vh;
  margin: 0 auto;
  overflow-x: hidden;
  box-sizing: border-box;
  background-color: white; }
  .item_navBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 15px; }
    .item_navBlock_wrapper_link {
      color: black;
      font-family: RobotoRegular;
      font-size: 30px;
      font-weight: normal;
      margin-top: 15px; }

.item_itemBlock {
  max-width: 1920px;
  width: 100%;
  margin: 50px auto; }
  .item_itemBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box; }
    .item_itemBlock_wrapper_h2 {
      color: black;
      font-size: 40px;
      font-family: RobotoRegular;
      font-weight: normal; }
    .item_itemBlock_wrapper_container {
      width: 100%;
      display: grid;
      grid-template-columns: 45% 45%;
      grid-column-gap: 5%;
      margin-top: 50px; }
      .item_itemBlock_wrapper_container_left {
        background-color: #C4C4C4;
        padding: 15px;
        display: flex;
        justify-content: center;
        align-items: center; }
        .item_itemBlock_wrapper_container_left_img {
          width: 75%; }
      .item_itemBlock_wrapper_container_right {
        display: flex;
        flex-direction: column;
        justify-content: space-between; }
        .item_itemBlock_wrapper_container_right_price {
          color: black;
          font-family: RobotoRegular;
          font-size: 40px; }
        .item_itemBlock_wrapper_container_right_btn {
          background-color: black;
          color: white;
          font-family: RobotoRegular;
          font-size: 30px;
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 50px; }
    .item_itemBlock_wrapper_descriptionBlock {
      margin-top: 50px; }
      .item_itemBlock_wrapper_descriptionBlock_h3 {
        font-family: RobotoRegular;
        font-size: 30px;
        color: black;
        font-weight: normal; }
      .item_itemBlock_wrapper_descriptionBlock_p {
        font-family: RobotoRegular;
        font-size: 20px;
        color: black;
        margin-top: 20px; }
  @media screen and (max-width: 1280px) {
    .item_itemBlock_wrapper {
      padding: 0 15px; } }
  @media screen and (max-width: 1000px) {
    .item_itemBlock_wrapper_container_right_btn {
      font-size: 24px;
      padding: 24px; } }
  @media screen and (max-width: 768px) {
    .item_itemBlock_wrapper_container {
      display: flex;
      flex-direction: column; }
      .item_itemBlock_wrapper_container_right {
        margin-top: 25px; }
        .item_itemBlock_wrapper_container_right_btn {
          margin-top: 15px; } }
  @media screen and (max-width: 380px) {
    .item_itemBlock_wrapper_container_right_btn {
      font-size: 18px; } }

.item_footerBlock {
  max-width: 1920px;
  width: 100%;
  margin-top: 100px;
  margin-bottom: 50px;
  box-sizing: border-box; }
  .item_footerBlock_wrapper {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    font-family: RobotoRegular;
    font-size: 10px;
    color: black; }
  @media screen and (max-width: 1280px) {
    .item_footerBlock {
      padding: 0 15px; } }
